import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { FormSelectChoice, InputSelectComponenentProps } from '../../../lib/types/types';
import { InputSelectComponent } from '../../InputSelect/InputSelectComponent';

const MilitaryStatusComponent = ({ value, isErrored, options, onChange }: InputSelectComponenentProps) => {
    return (
        <div className="sid-field sid-military-status">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label sid-field__label--required sid-military-status__label sid-l-space-btm-sm">
                    <FormattedHTMLMessage
                        id="status"
                        defaultMessage="Status"
                    />
                </div>
                <InputSelectComponent
                    isErrored={isErrored}
                    options={ options }
                    onChange={(status: FormSelectChoice) => onChange(status)}
                    value={value}
                />
            </label>
            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id="errorId.invalidStatus"
                            defaultMessage="Invalid military status"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

export { MilitaryStatusComponent };
