import { flatten } from 'flat';
import { Locale, StringMap, ProgramTheme } from '../types/types';
import { assertValidLocale } from '../types/assertions';
import { getOptions } from '../../options/options';
import { logger } from '../logger/logger';

export const getIntlMessages = (locale: Locale, programTheme: ProgramTheme = null): StringMap => {
    assertValidLocale(locale);

    // Some messages can be overwritten.
    const optionalMessages: Object = flatten(getOptions().messages);

    // react-intl doesn't allow the messages object to contain nested objects.
    // Use flatten() so we can access nested objects with dot-strings like 'foo.bar'
    let sheerIdMessages = {};

    if (programTheme) {
        try {
            sheerIdMessages = flatten(programTheme.intl.messages);
        } catch (e) {
            logger.error('Error accessing programTheme.intl.messages');
        }
    }

    return Object.assign({}, sheerIdMessages, optionalMessages) as StringMap;
};

export const getLocale = (programTheme: ProgramTheme = null): Locale => {
    let locale: Locale = getOptions().locale;

    if (programTheme) {
        try {
            locale = programTheme.intl.locale as Locale;
        } catch (e) {
            logger.error('Error accessing programTheme.intl.locale. Did the shape of an object/type change?');
        }
        try {
            assertValidLocale(locale);
        } catch (e) {
            logger.warn(`Invalid locale "${locale}" received from server. Using fallback "${getOptions().locale}" instead.`);
            locale = getOptions().locale;
        }
    }


    return locale;
};
