// TODO pass locale
async function fetchNewVerificationRequest(programId: DatabaseId, timeout = 1000): Promise<VerificationResponse> {
    const overriddenStep: MockStep = getRouteOverride();
    const mockResponse: VerificationResponse = getOverriddenMock(overriddenStep);

    if (mockResponse) {
        return mockResponse;
    }

    try {
        let response;
        const windowQueryString = window.location.search;
        const verificationId = getVerificationIdFromQueryString(windowQueryString);

        if (verificationId) {
            logger.info('fetchNewVerificationRequestActual: Calling back-end for existing verification request');
            response = fetchExistingVerificationRequest(verificationId);
        }

        if (!verificationId || response.currentStep === VerificationStepsEnum.error) {
            logger.info('fetchNewVerificationRequestActual: Calling back-end for new verification request');
            const url: string = getNewVerificationRequestUrl();
            const requestBody: NewVerificationRequest = {
                programId,
            };
            response = await PostJson(url, requestBody) as VerificationResponse;
        }
        return response;
    } catch (e) {
        logger.error(e);
    }
}

async function fetchExistingVerificationRequest(verificationId: string, timeout = 1000): Promise<VerificationResponse> {
    const overriddenStep: MockStep = getRouteOverride();
    const mockResponse: VerificationResponse = getOverriddenMock(overriddenStep);

    if (mockResponse) {
        return mockResponse;
    }

    try {
        logger.info('fetchNewVerificationRequestActual: Calling back-end for existing verification request');
        const url: string = getVerificationStatusUrl(verificationId);
        const response = await GetJson(url) as VerificationResponse;
        return response;
    } catch (e) {
        logger.error(e);
    }
}

async function fetchProgramTheme(programId: DatabaseId, timeout = 1000): Promise<ProgramTheme> {
    if (!getOptions().doFetchTheme) {
        return GetEmptyTheme();
    }
    logger.info('fetchProgramThemeActual: Calling back-end for program theme');

    try {
        const url: string = getProgramThemeUrl(programId);
        return await GetJson(url) as ProgramTheme;
    } catch (e) {
        logger.error(e);
    }
}

const studentViewModelToRequest = (viewModel: StudentPersonalInfoViewModel): StudentPersonalInfoRequest => viewModel;

const teacherViewModelToRequest = (viewModel: TeacherPersonalInfoViewModel): TeacherPersonalInfoRequest => viewModel;

const seniorViewModelToRequest = (viewModel: SeniorPersonalInfoViewModel): SeniorPersonalInfoRequest => viewModel;

// ActiveMilitaryPersonalInfoViewModel contains multiple keys we don't want so we can't return the full viewModel as the request body
const militaryStatusViewModelToRequest = (viewModel: ActiveMilitaryPersonalInfoViewModel): MilitaryStatusRequest => {
    return {
        status: viewModel.status,
    };
};

// ActiveMilitaryPersonalInfoViewModel contains 'status' which we don't want in the request so we can't return the full viewModel
const activeMilitaryViewModelToRequest = (viewModel: ActiveMilitaryPersonalInfoViewModel): ActiveMilitaryPersonalInfoRequest => {
    return {
        firstName: viewModel.firstName,
        lastName: viewModel.lastName,
        birthDate: viewModel.birthDate,
        email: viewModel.email,
        organization: viewModel.organization,
        metadata: viewModel.metadata,
    };
};

// InactiveMilitaryPersonalInfoViewModel contains 'status' which we don't want in the request so we can't return the full viewModel
const inactiveMilitaryViewModelToRequest = (viewModel: InactiveMilitaryPersonalInfoViewModel): InactiveMilitaryPersonalInfoRequest => {
    return {
        firstName: viewModel.firstName,
        lastName: viewModel.lastName,
        birthDate: viewModel.birthDate,
        email: viewModel.email,
        organization: viewModel.organization,
        dischargeDate: viewModel.dischargeDate,
        metadata: viewModel.metadata,
    };
};

async function submitStepCollectStudentPersonalInfo(
    previousResponse: StudentPersonalInfoResponse,
    viewModel: StudentPersonalInfoViewModel,
): Promise<VerificationResponse> {
    logger.info('fetchNewVerificationRequestActual: Calling back-end for new verification request');
    let response;

    const requestBody = studentViewModelToRequest(viewModel);
    try {
        response = await PostJson(previousResponse.submissionUrl, requestBody);
        return response;
    } catch (e) {
        logger.error(e);
        // At least show the error step...
        return getUnrecoverableErrorStep();
    }
}

