async function submitStepDocUpload(
    previousResponse: DocUploadResponse,
    viewModel: DocUploadViewModel,
): Promise<VerificationResponse> {
    logger.info('submitStepDocUpload', viewModel);
    let response;
    const requestBody: DocUploadRequest = viewModel;
    try {
        response = await PostFiles(previousResponse.submissionUrl, [requestBody.file1, requestBody.file2, requestBody.file3]);
        return response;
    } catch (e) {
        logger.error(e);
        // At least show the error step...
        return getUnrecoverableErrorStep();
    }
}

function getUnrecoverableErrorStep(): ErrorResponse {
    // TODO build an empty error step response with "unknown error" or some other appropriate error id.
    const response: ErrorResponse = {
        verificationId: '',
        currentStep: 'error',
        segment: null,
        subSegment: null,
        errorIds: ['unknownError'],
    };
    return response;
}

async function submitStep(
    stepName: VerificationStep,
    previousResponse: VerificationResponse,
    viewModel: ViewModel,
): Promise<VerificationResponse> {
    assertValidVerificationStepName(stepName);
    switch (stepName) {
        case VerificationStepsEnum.collectStudentPersonalInfo:
            return submitStepCollectStudentPersonalInfo(
                previousResponse as StudentPersonalInfoResponse,
                viewModel as StudentPersonalInfoViewModel,
            );
        case VerificationStepsEnum.collectSeniorPersonalInfo:
            return submitStepCollectSeniorPersonalInfo(
                previousResponse as SeniorPersonalInfoResponse,
                viewModel as SeniorPersonalInfoViewModel,
            );
        case VerificationStepsEnum.collectTeacherPersonalInfo:
            return submitStepCollectTeacherPersonalInfo(
                previousResponse as TeacherPersonalInfoResponse,
                viewModel as TeacherPersonalInfoViewModel,
            );
        case VerificationStepsEnum.collectMilitaryStatus:
            return submitStepCollectMilitaryStatus(
                previousResponse as MilitaryStatusResponse,
                viewModel as ActiveMilitaryPersonalInfoViewModel,
            );
        case VerificationStepsEnum.collectActiveMilitaryPersonalInfo:
            return submitStepCollectActiveMilitaryPersonalInfo(
                previousResponse as ActiveMilitaryPersonalInfoResponse,
                viewModel as ActiveMilitaryPersonalInfoViewModel,
            );
        case VerificationStepsEnum.collectInactiveMilitaryPersonalInfo:
            return submitStepCollectInactiveMilitaryPersonalInfo(
                previousResponse as InactiveMilitaryPersonalInfoResponse,
                viewModel as InactiveMilitaryPersonalInfoViewModel,
            );
        case VerificationStepsEnum.docUpload:
            return submitStepDocUpload(
                previousResponse as DocUploadResponse,
                viewModel as DocUploadViewModel,
            );
    }
}

export const VerificationApiClient = {
    fetchNewVerificationRequest,
    fetchExistingVerificationRequest,
    fetchProgramTheme,
    submitStepCollectStudentPersonalInfo,
    submitStepDocUpload,
    submitStep,
};
