import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import { FormattedHTMLMessage } from 'react-intl';
import { Segment, VerificationService } from '../../lib/types/types';
import { assertValidSegmentName } from '../../lib/types/assertions';
import { SegmentEnum } from '../../lib/types/runtimeTypes';
import { getOptions } from '../../options/options';

const options = getOptions();

const getFaqUrl = (segment: Segment):string => {
    assertValidSegmentName(segment);
    switch (segment) {
        case SegmentEnum.STUDENT:
            return options.urlStudentFaq;
        case SegmentEnum.SENIOR:
            return options.urlSeniorFaq;
        case SegmentEnum.TEACHER:
            return options.urlTeacherFaq;
        case SegmentEnum.MILITARY:
            return options.urlMilitaryFaq;
    }
};

interface Props {
    verificationService: VerificationService;
}


const FaqLinkComponent: React.SFC<Props> = ({ verificationService }) => {
    const segment = verificationService.verificationResponse.segment;
    const programId = verificationService.programId;
    const faqHref = `${getFaqUrl(segment)}?pid=${programId}`;

    return (
        <a className="sid-faq sid-link" href={faqHref} target="_blank">
            <FormattedHTMLMessage id="sheeridFaqs" defaultMessage="SheerID FAQs" />
        </a>
    );
};

export { FaqLinkComponent };
