/**
 * TODO - preamble
 *
 * assertions.ts
 *
 * Useful for runtime checking. Either pass a valid arg, or bail out by throwing an Error.
 */
import { MockStep, VerificationStep, Segment, DatabaseId, Locale, FieldId } from '../../lib/types/types';
import { Locales, MockSteps, VerificationSteps, Segments, FieldIds, TryAgainSteps } from '../../lib/types/runtimeTypes';

export const assertValidVerificationStepName = (candidate: VerificationStep) => {
    // TODO - only in dev mode
    if (VerificationSteps.indexOf(candidate) < 0) {
        throw new Error(`Expected valid verification step name but received "${candidate}".`);
    }
};

export const assertValidMockStepName = (candidate: MockStep) => {
    // TODO - only in dev mode
    if (MockSteps.indexOf(candidate) < 0) {
        throw new Error(`Expected valid verification step name but received "${candidate}".`);
    }
};

export const assertValidSegmentName = (candidate: Segment) => {
    // TODO - only in dev mode
    if (Segments.indexOf(candidate) < 0) {
        throw new Error(`Expected valid segment name but received "${candidate}".`);
    }
};

export const assertValidLocale = (candidate: Locale) => {
    // TODO - only in dev mode
    if (Locales.indexOf(candidate) < 0) {
        throw new Error(`Expected valid locale name but received "${candidate}". Valid Locales are [${Locales.join(', ')}]`);
    }
};

export const assertValidHtmlElement = (element: HTMLElement) => {
    if (!(element && element.nodeType === Node.ELEMENT_NODE)) {
        throw new Error(`Expected argument of type Node.ELEMENT_NODE but received "${typeof element} ${element.nodeType}"`);
    }
};

export const assertValidProgramId = (programId: DatabaseId) => {
    if (!(programId && typeof programId === 'string')) {
        throw new Error(`Expected programId argument of type string. Received "${typeof programId}"`);
    }
};

export const assertValidFieldId = (candidate: FieldId) => {
    if (FieldIds.indexOf(candidate) < 0) {
        throw new Error(`Expected valid field ID but received ${candidate}. Valid FieldIds are [${FieldIds.join(', ')}]`);
    }
};

export const assertValidTryAgainStep = (candidate: VerificationStep) => {
    if (TryAgainSteps.indexOf(candidate) < 0) {
        throw new Error(`Expected valid try again step but received ${candidate}. Valid TryAgainSteps are [${TryAgainSteps.join(', ')}]`);
    }
};
