import * as React from 'react';
import { FormattedHTMLMessage, intlShape, injectIntl } from 'react-intl';
import { produce } from 'immer';

import { logger } from '../../lib/logger/logger';
import { VerificationServiceShape } from '../../lib/types/propTypes';
import { MilitaryStatusComponent as MilitaryStatus } from '../FormFields/MilitaryStatus/MilitaryStatusComponent';
import { BranchOfServiceComponent as BranchOfService } from '../FormFields/BranchOfService/BranchOfServiceComponent';
import { FirstNameComponent as FirstName } from '../FormFields/FirstName/FirstNameComponent';
import { LastNameComponent as LastName } from '../FormFields/LastName/LastNameComponent';
import { BirthDateComponent as BirthDate } from '../FormFields/BirthDate/BirthDateComponent';
import { EmailComponent as Email } from '../FormFields/Email/EmailComponent';
import { FormFooterComponent as FormFooter } from '../FormFooter/FormFooterComponent';
import { VerificationStepsEnum, MilitaryStatusDefaultMessagesEnum } from '../../lib/types/runtimeTypes';
import { validateFieldById, getFieldValidationErrors, isFormFilled, isFormErrored } from '../../lib/types/validators';
import { FieldId, FormSelectChoice, VerificationService, ActiveMilitaryPersonalInfoViewModel, FieldValidationErrors, MilitaryStatusResponse, Organization, FieldContent } from '../../lib/types/types';
import { HowDoesVerifyingWorkComponent as HowDoesVerifyingWork } from '../HowDoesVerifyingWork/HowDoesVerifyingWorkComponent';
import { OptInComponent } from '../FormFields/OptIn/OptInComponent';


interface StepActiveMilitaryPersonalInfoComponentProps {
    intl: any;
    verificationService: VerificationService;
}

export const StepActiveMilitaryPersonalInfo = ({ intl, verificationService }: StepActiveMilitaryPersonalInfoComponentProps) => {
    const viewModel = verificationService.viewModel as ActiveMilitaryPersonalInfoViewModel;
    const previousViewModel = verificationService.previousViewModel as ActiveMilitaryPersonalInfoViewModel;
    const fieldValidationErrors: FieldValidationErrors = verificationService.fieldValidationErrors;

    const updateViewModel = (key, value) => {
        const nextState = produce(viewModel, (draft) => {
            draft[key] = value;
        });
        verificationService.updateViewModel(nextState);
    };

    const updateViewModelOrganization = (choice: Organization) => {
        const nextState = produce(viewModel, (draft: ActiveMilitaryPersonalInfoViewModel) => {
            draft.organization.id = choice.id;
            draft.organization.name = choice.name;
        });
        verificationService.updateViewModel(nextState);
    };

    const updateFieldValidationErrorsByFieldId = (fieldId: FieldId, value: FieldContent) => {
        const nextState = produce(fieldValidationErrors, (draft) => {
            if (draft[fieldId]) {
                draft[fieldId] = validateFieldById(fieldId, value);
            }
        });
        updateFieldValidationErrors(nextState);
    };

    const updateFieldValidationErrors = (fieldValidationErrors: FieldValidationErrors) => {
        verificationService.updateFieldValidationErrors(fieldValidationErrors);
    };

    const submitStep = () => {
        const nextState = produce(viewModel, () => {
        });
        logger.info('StepMilitaryStatusComponent submitting form');
        verificationService.submitStep(
            VerificationStepsEnum.collectMilitaryStatus,
            nextState,
            verificationService.previousVerificationResponse || verificationService.verificationResponse,
        );
    };

    const getAvailableMilitaryStatuses = ():FormSelectChoice[] => {
        const availableStatusesResponse =
            (verificationService.verificationResponse as MilitaryStatusResponse).availableStatuses ||
            (verificationService.previousVerificationResponse as MilitaryStatusResponse).availableStatuses;
        const availableStatuses: FormSelectChoice[] = [];

        for (const status of availableStatusesResponse) {
            availableStatuses.push({
                value: status,
                label: intl.formatMessage({ id: status, defaultMessage: MilitaryStatusDefaultMessagesEnum[status] }),
            });
        }
        return availableStatuses;
    };

    const submitForm = () => {
        const currentFieldValidationErrors = getFieldValidationErrors(viewModel);
        const isFormValid = !isFormErrored(currentFieldValidationErrors);

        if (isFormValid) {
            const nextState = produce(viewModel, () => {});
            logger.info('StepActiveMilitaryPersonalInfoComponent submitting form');
            verificationService.submitStep(
                VerificationStepsEnum.collectActiveMilitaryPersonalInfo,
                nextState,
                verificationService.verificationResponse,
            );
        } else {
            updateFieldValidationErrors(currentFieldValidationErrors);
        }
    };

    if ((viewModel.status && previousViewModel && previousViewModel.status !== viewModel.status) ||
        (viewModel.status && verificationService.verificationResponse.currentStep === VerificationStepsEnum.collectMilitaryStatus)) {
        submitStep();
    }


    return (
        <div id="sid-step-active-military-personal-info" className="sid-l-container">
            <div className="sid-header">
                <div className="sid-header__title sid-l-horz-center">
                    <FormattedHTMLMessage id="step.personalInfo.title" defaultMessage="Unlock this Military-Only Offer" />
                </div>
                <div className="sid-header__subtitle sid-l-horz-center">
                    <FormattedHTMLMessage id="step.personalInfo.subtitle"
                        defaultMessage="Verify you're a member of the armed forces." />
                    &nbsp;
                    <HowDoesVerifyingWork verificationService={verificationService} />
                </div>
            </div>

            <MilitaryStatus
                value={{
                    value: (verificationService.viewModel as ActiveMilitaryPersonalInfoViewModel).status,
                    label: MilitaryStatusDefaultMessagesEnum[(verificationService.viewModel as ActiveMilitaryPersonalInfoViewModel).status],
                }}
                isErrored={!!fieldValidationErrors.status}
                options={ getAvailableMilitaryStatuses() }
                    onChange={
                        (status: FormSelectChoice) => {
                            updateViewModel('status', status ? status.value : '');
                            updateFieldValidationErrorsByFieldId('status', status ? (status.value as string) : '');
                        }
                    }
            />

            <BranchOfService
                isErrored={!!fieldValidationErrors.organization}
                onChange={
                    (choice: Organization) => {
                        updateViewModelOrganization(choice);
                        updateFieldValidationErrorsByFieldId('organization', choice);
                    }
                }
                value={(verificationService.viewModel as ActiveMilitaryPersonalInfoViewModel).organization}
            />

            <div className="sid-names">
                <FirstName
                    value={viewModel.firstName}
                    isErrored={!!fieldValidationErrors.firstName}
                    onChange={
                        (newValue) => {
                            updateViewModel('firstName', newValue);
                            updateFieldValidationErrorsByFieldId('firstName', newValue);
                        }
                    }
                />

                <LastName
                    value={viewModel.lastName}
                    isErrored={!!fieldValidationErrors.lastName}
                    onChange={
                        (newValue) => {
                            updateViewModel('lastName', newValue);
                            updateFieldValidationErrorsByFieldId('lastName', newValue);
                        }
                    }
                />
            </div>

            <BirthDate
                isErrored={!!fieldValidationErrors.birthDate}
                onChange={(newValue) => {
                    updateViewModel('birthDate', newValue);
                    updateFieldValidationErrorsByFieldId('birthDate', newValue);
                }}
                value={viewModel.birthDate}
            />

            <Email
                value={viewModel.email}
                isErrored={!!fieldValidationErrors.email}
                explanation={
                    <FormattedHTMLMessage
                        id="emailExplanation"
                        defaultMessage="Personal email address is recommended"
                    />
                }
                onChange={
                    (newValue) => {
                        updateViewModel('email', newValue);
                        updateFieldValidationErrorsByFieldId('email', newValue);
                    }
                }
            />

            {viewModel.metadata && viewModel.metadata.hasOwnProperty('optin') ?
                <OptInComponent
                    value={viewModel.metadata.optin}
                    onChange={
                        (newValue) => {
                            updateViewModel('metadata', { optin: newValue });
                        }
                    }
                />
                : null
            }

            <div className="sid-form-region sid-submit-wrapper sid-l-space-top-md">
                <button
                    onClick={submitForm}
                    type="submit"
                    className={`sid-btn sid-btn--dark sid-l-full-width ${!isFormFilled(viewModel) ? 'sid-btn--disabled-like' : ''}`}
                    aria-label="submit"
                >
                    <FormattedHTMLMessage id="verifyMyMilitaryStatus" defaultMessage="Verify My Military Status" />
                </button>
            </div>

            <FormFooter verificationService={verificationService}>
                <FormattedHTMLMessage
                    id="militaryInfoShared"
                    defaultMessage={'Information entered here will be used for verification purposes.'}
                />
            </FormFooter>
        </div>
    );
};

// TODO: TypeScript complains about verificationService
// StepActiveMilitaryPersonalInfo.propTypes = {
//     verificationService: VerificationServiceShape.isRequired,
// };

export const StepActiveMilitaryPersonalInfoComponent = injectIntl(StepActiveMilitaryPersonalInfo);
