import * as React from 'react';
import * as propTypes from 'prop-types';

interface Props {
    styles?: string;
    children?: any;
}

const CustomCssWrapperComponent: React.SFC<Props> = props => (
    <div>
        {props.styles ?
            (<style>
                {props.styles}
            </style>)
        : null}

        {props.children ?
            props.children
        : null}
    </div>
);

CustomCssWrapperComponent.propTypes = {
    styles: propTypes.string,
    children: propTypes.any,
};

CustomCssWrapperComponent.defaultProps = {
    styles: '',
    children: '',
};

export { CustomCssWrapperComponent };
