import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';
import Downshift from 'downshift';

import { OrganizationShape } from '../lib/types/propTypes';
import { FetchOrganizationsComponent } from './FetchOrganizationsComponent';
import { FetchOrganizationTypes, Organization } from '../lib/types/types';
import { SegmentEnum } from '../lib/types/runtimeTypes';


interface StudentTypeaheadProps {
    className?: string;
    minimumSearchValueLength?: number;
    onChange: any;
    value?: Organization;
}

const FETCH_ORGANIZATION_TYPE: FetchOrganizationTypes = SegmentEnum.STUDENT;

const StudentTypeahead = ({ onChange, className, minimumSearchValueLength, value }: StudentTypeaheadProps) => {
    const itemToString = (item: Organization) => {
        return item ? item.name : '';
    };

    const handleOnLoaded = (loadedOrganizations, clearItems, setHighlightedIndex, setItemCount) => {
        clearItems();
        if (loadedOrganizations) {
            setHighlightedIndex(loadedOrganizations.length ? 0 : null);
            setItemCount(loadedOrganizations.length);
        }
    };

    return (
        <Downshift
            onChange={onChange}
            itemToString={itemToString}
            initialSelectedItem={value && value.id && value.name ? value : undefined}>
            {({
                clearItems,
                getInputProps,
                getItemProps,
                getMenuProps,
                highlightedIndex,
                inputValue,
                isOpen,
                openMenu,
                setHighlightedIndex,
                setItemCount,
            }) =>
            <div className={'sid-organization-list'}>
                <input
                    {...getInputProps({ onFocus: openMenu })}
                    className={`sid-l-full-width sid-text-input ${className}`}
                />
                {isOpen
                    ?
                    <FetchOrganizationsComponent
                        fetchOrganizationType={ FETCH_ORGANIZATION_TYPE }
                        getItemProps={ getItemProps }
                        getMenuProps={ getMenuProps }
                        highlightedIndex={ highlightedIndex }
                        itemToString={ itemToString }
                        minimumSearchValueLength={ minimumSearchValueLength }
                        onLoaded={ loadedOrganizations => handleOnLoaded(loadedOrganizations, clearItems, setHighlightedIndex, setItemCount) }
                        searchValue={ inputValue }
                    />
                : null}
            </div>}
        </Downshift>
    );
};


StudentTypeahead.propTypes = {
    className: propTypes.string,
    minimumSearchValueLength: propTypes.number,
    onChange: propTypes.func.isRequired,
    value: OrganizationShape,
};

StudentTypeahead.defaultProps = {
    className: undefined,
    value: undefined,
    minimumSearchValueLength: 3,
};

export const StudentTypeaheadComponent = StudentTypeahead;
