import { OrganizationSearchResp } from '../types/types';

import { logger } from '../logger/logger';
import { getOptions } from '../../options/options';

const name = 'name';

export async function fetchStudentOrganizations(searchTerm: string): Promise<OrganizationSearchResp> {
    const url = `${getOptions().restApi.serviceUrl}${getOptions().restApi.resources.organization.student}?${name}=${searchTerm}`;
    return fetchOrganization(url);
}

export async function fetchMilitaryOrganizations(searchTerm: string): Promise<OrganizationSearchResp> {
    const url = `${getOptions().restApi.serviceUrl}${getOptions().restApi.resources.organization.military}?${name}=${searchTerm}`;
    return fetchOrganization(url);
}

export async function fetchTeacherOrganizations(searchTerm: string): Promise<OrganizationSearchResp> {
    const url = `${getOptions().restApi.serviceUrl}${getOptions().restApi.resources.organization.teacher}?${name}=${searchTerm}`;
    return fetchOrganization(url);
}

async function fetchOrganization(url: string) : Promise<OrganizationSearchResp> {
    // TODO use Network.js -> GetJson or something
    try {
        const response = await fetch(url, {
            headers: new Headers({
                'Content-Type': 'application/json',
                Accept: 'application/json',
            }),
        });
        const responseEntries = await response.json();
        return responseEntries.map(entry => ({ id: entry.id, name: entry.name }));
    } catch (e) {
        logger.error(e);
    }
}
