import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { FormFieldComponenentProps } from '../../../lib/types/types';

const LastNameComponent = ({ value, isErrored, onChange }: FormFieldComponenentProps) => {
    return (
        <div className="sid-last-name">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label sid-field__label--required sid-l-space-btm-sm">
                    <FormattedHTMLMessage
                        id="lastName"
                        defaultMessage="Last name"
                    />
                </div>
                <input
                    className={
                        `sid-text-input sid-text-input--required
                            sid-l-full-width ${isErrored ? 'sid-text-input--error' : ''}`
                    }
                    id="sid-last-name"
                    name="sid-last-name"
                    type="text"
                    value={value}
                    onChange={e => onChange(e.target.value)}
                />
            </label>
            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id="errorId.invalidLastName"
                            defaultMessage="Invalid last name"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

export {  LastNameComponent };
