import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { FormSelectChoice, FormFieldComponenentProps } from '../../../lib/types/types';
import { TeacherTypeaheadComponent } from '../../TeacherTypeaheadComponent';

const TeacherSchoolComponent = ({ value, isErrored, onChange }: FormFieldComponenentProps) => {
    return (
        <div className="sid-field sid-teacher-school-id">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label sid-field__label--required sid-l-space-btm-sm">
                    <FormattedHTMLMessage
                        id="schoolName"
                        defaultMessage="School name"
                    />
                </div>
                <TeacherTypeaheadComponent
                        className={isErrored ? 'sid-text-input--error' : ''}
                        onChange={ (choice: FormSelectChoice) => onChange(choice) }
                        minimumSearchValueLength={3}
                        value={value}
                    />
            </label>
            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id="errorId.invalidOrganization"
                            defaultMessage="You must select an organization from the list"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

export {  TeacherSchoolComponent };
