import { ReduxState } from '../../lib/types/types';
import { getFieldValidationErrorsEmpty } from '../../lib/types/empties';
import { VerificationStepsEnum } from '../../lib/types/runtimeTypes';
import { setMetadata, getMetadata } from '../metadata/metadata';

const initialState: ReduxState = {
    programId: undefined,
    isLoading: true,
    isErrored: false,
    viewModel: undefined,
    previousViewModel: undefined,
    previousVerificationResponse: undefined,
    verificationResponse: undefined,
    fieldValidationErrors: getFieldValidationErrorsEmpty(),
};

// TODO move reducer somewhere better
export const verificationServiceReducer = (state: ReduxState = initialState, action) => {
    switch (action.type) {
        case 'PROGRAM_ID':
            return {
                ...state,
                programId: action.programId,
            };
        case 'VERIFICATION_RESPONSE':
            let previousVerificationResponse;
            if (state.verificationResponse && state.verificationResponse.currentStep === VerificationStepsEnum.collectMilitaryStatus) {
                previousVerificationResponse = state.verificationResponse;
            } else if (state.previousVerificationResponse) {
                previousVerificationResponse = state.previousVerificationResponse;
            }

            return {
                ...state,
                previousVerificationResponse,
                verificationResponse: action.verificationResponse,
            };
        case 'FIELD_VALIDATION_ERRORS':
            return {
                ...state,
                fieldValidationErrors: action.fieldValidationErrors,
            };
        case 'VIEW_MODEL':
            const currentResponse = state.verificationResponse;
            const currentStep = currentResponse ? currentResponse.currentStep : undefined;

            if (currentStep &&
                currentStep !== VerificationStepsEnum.docUpload &&
                currentStep !== VerificationStepsEnum.pending &&
                currentStep !== VerificationStepsEnum.error &&
                currentStep !== VerificationStepsEnum.docReviewLimitExceeded &&
                currentStep !== VerificationStepsEnum.success) {

                const previousViewModel = state.viewModel;
                const actionMetadata = action.viewModel ? action.viewModel.metadata : undefined;
                if (actionMetadata) {
                    setMetadata(actionMetadata);
                }
                const metadata = getMetadata();

                return {
                    ...state,
                    previousViewModel,
                    viewModel: { ...action.viewModel, metadata },
                };
            }
            return {
                ...state,
                viewModel: action.viewModel,
            };
        case 'PROGRAM_THEME':
            return {
                ...state,
                programTheme: action.programTheme,
            };
        case 'IS_LOADING':
            return {
                ...state,
                isLoading: action.isLoading,
            };
        case 'IS_ERRORED':
            return {
                ...state,
                isErrored: action.isErrored,
            };
        default:
            return state;
    }
};
