import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import { Provider } from 'react-redux';
import { createStore } from 'redux';

import { FormWrapperComponent as FormWrapper } from '../../components/FormWrapper/FormWrapperComponent';
import { NewVerificationComponent } from '../../components/NewVerificationComponent';

import { verificationServiceReducer } from '../../lib/VerificationService/verificationServiceReducer';
import { DatabaseId } from '../../lib/types/types';
import { assertValidProgramId } from '../../lib/types/assertions';

const store = createStore(
    verificationServiceReducer,
    // @ts-ignore
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(),
);

interface VerificationFormProps {
    programId: DatabaseId;
}

export const VerificationForm = ({ programId }: VerificationFormProps) => {
    assertValidProgramId(programId);
    store.dispatch({ programId, type: 'PROGRAM_ID' });

    return (
        <Provider store={store}>
            <FormWrapper>
                {<NewVerificationComponent programId={programId} />}
            </FormWrapper>
        </Provider>
    );
};
