import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { VerificationServiceShape } from '../../lib/types/propTypes';
import { VerificationService, SuccessResponse } from '../../lib/types/types';
import { redirectTo } from '../../lib/routing/Url';
import { CopyToClipboard } from '../CopyToClipboard/CopyToClipboardComponent';

interface StepSuccessProps {
    verificationService: VerificationService;
}

const StepSuccessComponent = ({ verificationService }: StepSuccessProps) => {
    let showRedirect: boolean;
    let redirectDestination: string;

    try {
        showRedirect = verificationService.programTheme.intl.messages.step.success.redirectUrl.length > 0
            && verificationService.programTheme.intl.messages.step.success.redirectButtonText.length > 0;
        redirectDestination = verificationService.programTheme.intl.messages.step.success.redirectUrl;
    } catch (e) {
        showRedirect = false;
    }

    return (
        <div id="sid-step-success" className="sid-l-container">
            <div className="sid-header sid-l-horz-center sid-l-space-top-md">
                <img
                    className="sid-l-lead-image"
                    alt="design element"
                    src="https://s3.amazonaws.com/com.sheerid.resources/common/images/2018/icons/circle-check.svg"
                />
                <div className="sid-header__title sid-l-horz-center sid-l-space-top-md">
                    <FormattedHTMLMessage id="step.success.title" defaultMessage="Success! You've been verified." />
                </div>
                <div className="sid-header__subtitle sid-l-horz-center sid-l-two-thirds-width">
                    <FormattedHTMLMessage id="step.success.subtitle" defaultMessage={
                        `Here is your personal coupon code.
                        It is unique to you and can only be used once..`
                    } />
                </div>
            </div>

            <div className="sid-reward-code sid-l-space-top-lg sid-h-jumbo-text sid-l-horz-center">
                {(verificationService.verificationResponse as SuccessResponse).rewardCode}
            </div>

            <div className="sid-l-space-top-md sid-h-bold-text sid-l-horz-center">
                <CopyToClipboard
                    textToCopy={(verificationService.verificationResponse as SuccessResponse).rewardCode}
                >
                    <FormattedHTMLMessage id="copyCouponCode" defaultMessage="Copy Coupon code" />
                </CopyToClipboard>
            </div>
            <div className="sid-footer sid-l-horz-center sid-l-space-top-lg">
                <FormattedHTMLMessage id="step.success.emailExplain" defaultMessage={`
                    We have emailed you a copy so you can use your now or later. Look for the email from verify@sheerid.com
                `} />
            </div>

            {
                (showRedirect) ?
                    <div className="sid-submit sid-l-space-top-md">
                        <a href={redirectDestination} target="_parent" className="sid-btn sid-btn--dark">
                            <FormattedHTMLMessage id="step.success.redirectButtonText" defaultMessage="Use code now" />
                        </a>
                    </div> : ''
            }

            <div className="sid-footer sid-l-horz-center sid-l-space-top-lg sid-h-small-text">
                <FormattedHTMLMessage id="step.success.verificationOnly"
                    defaultMessage={`
                        SheerID handles verification only.
                        Now that you've been approved, please direct all questions about the
                        promotion terms and/or code use to {company} customer service.
                    `}
                    values={{
                        company: verificationService.programTheme.intl.messages.companyName || '{Company}',
                    }}
                />
            </div>
        </div>
    );
};

StepSuccessComponent.propTypes = {
    verificationService: VerificationServiceShape.isRequired,
};

export { StepSuccessComponent };
