import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { PoweredByComponent } from './PoweredBy/PoweredByComponent';
import { VerificationService } from '../lib/types/types';
import { logger } from '../lib/logger/logger';

const defaultSubtitle = `SheerID is reviewing your documents, to confirm your
                        status. This process may take up to 20 minutes.
                        Wait here or let us notify you of the outcome. `;

const defaultSubtitle2 = 'You will be contacted via email address.';

interface StepPendingProps {
    verificationService: VerificationService;
}

const StepPendingComponent = ({ verificationService }: StepPendingProps) => {
    const programId = verificationService.programId;
    const viewModel = verificationService.viewModel;
    const verificationId = verificationService.verificationResponse.verificationId;

    const pollForVerificationServiceUpdates = () => {
        setTimeout(
            () => {
                verificationService.fetchExistingVerificationRequest(programId, verificationId, viewModel);
            },
            20000,
        );
    };

    pollForVerificationServiceUpdates();

    return (
        <div id="sid-step-pending" className="sid-l-container--narrow">
            <div className="sid-header sid-l-horz-center sid-l-space-top-xl">
                <img
                    className="sid-l-lead-image"
                    alt="design element"
                    src="https://s3.amazonaws.com/com.sheerid.resources/common/images/2018/icons/doc-pending.svg"
                />
                <div className="sid-header__title sid-l-horz-center sid-l-space-top-md">
                    <FormattedHTMLMessage id="step.pending.title" defaultMessage="Under Review" />
                </div>
                <div className="sid-header__subtitle-one">
                    <FormattedHTMLMessage
                        id="step.pending.subtitle"
                        defaultMessage={defaultSubtitle}
                    />
                </div>
                <div className="sid-header__subtitle-two sid-h-small-text sid-l-space-top-md">
                    <FormattedHTMLMessage
                        id="step.pending.subtitle2"
                        defaultMessage={defaultSubtitle2}
                    />
                </div>
            </div>

            <div className="sid-footer sid-l-horz-center sid-l-space-top-xl sid-h-small-text">
                <PoweredByComponent verificationService={verificationService} isCentered />
            </div>
        </div>
    );
};

export { StepPendingComponent };
