import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import { FormattedHTMLMessage } from 'react-intl';
import { VerificationServiceShape } from '../lib/types/propTypes';
import { VerificationService } from '../lib/types/types';


interface TryAgainButtonComponentProps {
    verificationService: VerificationService;
}

const TryAgainButtonComponent = ({ verificationService }: TryAgainButtonComponentProps) => (
    <button
        type="button"
        className="sid-btn sid-btn-light sid-submit"
        aria-label="submit"
        onClick={ () => verificationService.fetchNewVerificationRequest(verificationService.programId, verificationService.previousViewModel) }
        onKeyPress={ () => verificationService.fetchNewVerificationRequest(verificationService.programId, verificationService.previousViewModel) }
        tabIndex={0}
    >
        <FormattedHTMLMessage id="tryAgain" defaultMessage="Try again" />
    </button>
);


TryAgainButtonComponent.propTypes = {
    verificationService: VerificationServiceShape.isRequired,
};

export { TryAgainButtonComponent };
