import { produce } from 'immer';

import {
    StudentPersonalInfoViewModel,
    SeniorPersonalInfoViewModel,
    TeacherPersonalInfoViewModel,
    VerificationResponse,
    ViewModel,
    DocUploadViewModel,
    ActiveMilitaryPersonalInfoViewModel,
    InactiveMilitaryPersonalInfoViewModel,
} from '../types/types';

import { VerificationStepsEnum } from '../types/runtimeTypes';

import {
    stuPInfoReqEmpty,
    seniorPInfoReqEmpty,
    teaPInfoReqEmpty,
    activeMilitaryPInfoReqEmpty,
    inactiveMilitaryPInfoReqEmpty,
    docUploadEmpty,
} from '../../lib/types/empties';

import { assertValidVerificationStepName } from '../types/assertions';

/**
 * Return an empty request object as a starting-point for our run-time model
 */
export const getEmptyViewModel = (verificationResponse: VerificationResponse): ViewModel => {
    assertValidVerificationStepName(verificationResponse.currentStep);
    switch (verificationResponse.currentStep) {
        case VerificationStepsEnum.collectStudentPersonalInfo:
            const stuPInfoReqEmptyClone: StudentPersonalInfoViewModel = produce(stuPInfoReqEmpty, () => {});
            return stuPInfoReqEmptyClone;
        case VerificationStepsEnum.collectSeniorPersonalInfo:
            const seniorPInfoReqEmptyClone: SeniorPersonalInfoViewModel = produce(seniorPInfoReqEmpty, () => {});
            return seniorPInfoReqEmptyClone;
        case VerificationStepsEnum.collectTeacherPersonalInfo:
            const teaPInfoReqEmptyClone: TeacherPersonalInfoViewModel = produce(teaPInfoReqEmpty, () => {});
            return teaPInfoReqEmptyClone;
        case VerificationStepsEnum.collectMilitaryStatus: // Get ActiveMilitaryPersonalInfoViewModel
        case VerificationStepsEnum.collectActiveMilitaryPersonalInfo:
            const activeMilitaryPInfoEmptyClone: ActiveMilitaryPersonalInfoViewModel = produce(activeMilitaryPInfoReqEmpty, () => {});
            return activeMilitaryPInfoEmptyClone;
        case VerificationStepsEnum.collectInactiveMilitaryPersonalInfo:
            const inactiveMilitaryPInfoEmptyClone: InactiveMilitaryPersonalInfoViewModel = produce(inactiveMilitaryPInfoReqEmpty, () => {});
            return inactiveMilitaryPInfoEmptyClone;
        case VerificationStepsEnum.docUpload:
            const docUploadEmptyClone: DocUploadViewModel = produce(docUploadEmpty, () => {});
            return docUploadEmptyClone;
        default:
            return undefined;
    }
};
