import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';
import { FormattedHTMLMessage, InjectedIntlProps, injectIntl } from 'react-intl';
import { logger } from '../../../lib/logger/logger';
import { FormFieldComponenentProps, FormSelectChoice } from '../../../lib/types/types';
import { InputSelectComponent } from '../../InputSelect/InputSelectComponent';

export const DischargeDate = ({
    onChange,
    intl,
    isErrored,
    value,
}: FormFieldComponenentProps & InjectedIntlProps) => {
    const dischargeDateParts = value.split('-');
    const dischargeDateYear = Number.parseInt(dischargeDateParts[0], 10) || undefined;
    const dischargeDateMonth = Number.parseInt(dischargeDateParts[1], 10) || undefined;
    const dischargeDateDay = Number.parseInt(dischargeDateParts[2], 10) || undefined;

    const minDischargeYear = 1900;
    const thisYear = new Date().getFullYear();

    const months: FormSelectChoice[] = [
        { value: '1', label: intl.formatMessage({ id: 'dateTime.january', defaultMessage: 'January' }) },
        { value: '2', label: intl.formatMessage({ id: 'dateTime.february', defaultMessage: 'February' }) },
        { value: '3', label: intl.formatMessage({ id: 'dateTime.march', defaultMessage: 'March' }) },
        { value: '4', label: intl.formatMessage({ id: 'dateTime.april', defaultMessage: 'April' }) },
        { value: '5', label: intl.formatMessage({ id: 'dateTime.may', defaultMessage: 'May' }) },
        { value: '6', label: intl.formatMessage({ id: 'dateTime.june', defaultMessage: 'June' }) },
        { value: '7', label: intl.formatMessage({ id: 'dateTime.july', defaultMessage: 'July' }) },
        { value: '8', label: intl.formatMessage({ id: 'dateTime.august', defaultMessage: 'August' }) },
        { value: '9', label: intl.formatMessage({ id: 'dateTime.september', defaultMessage: 'September' }) },
        { value: '10', label: intl.formatMessage({ id: 'dateTime.october', defaultMessage: 'October' }) },
        { value: '11', label: intl.formatMessage({ id: 'dateTime.november', defaultMessage: 'November' }) },
        { value: '12', label: intl.formatMessage({ id: 'dateTime.december', defaultMessage: 'December' }) },
    ];

    const updateDischargeMonth = (month) => {
        if (month === '' || month === null) {
            onChange(`${dischargeDateYear}--${dischargeDateDay}`);
        }

        const newVal = Number.parseInt(month, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= 12) {
            onChange(`${dischargeDateYear}-${month}-${dischargeDateDay}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid discharge month`);
        }
    };

    const updateDischargeDay = (day) => {
        if (day === '') {
            onChange(`${dischargeDateYear}-${dischargeDateMonth}-`);
        }
        const newVal = Number.parseInt(day, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= 31) {
            onChange(`${dischargeDateYear}-${dischargeDateMonth}-${newVal}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid discharge day`);
        }
    };

    const updateDischargeYear = (year) => {
        if (year === '') {
            onChange(`-${dischargeDateMonth}-${dischargeDateDay}`);
        }
        const newVal = Number.parseInt(year, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= thisYear) {
            onChange(`${newVal}-${dischargeDateMonth}-${dischargeDateDay}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid discharge year`);
        }
    };

    const getDischargeDate = () => {
        onChange(`${dischargeDateYear}-${dischargeDateMonth}-${dischargeDateDay}`);
    };

    return (
        <div className="sid-field sid-discharge-date">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label-with-explanation sid-l-space-btm-sm">
                    <div className="sid-field__label sid-field__label--required">
                        <FormattedHTMLMessage
                            id="dischargeDate"
                            defaultMessage="Discharge date"
                        />
                    </div>
                </div>

                <div className="sid-discharge-date__inputs">
                    <InputSelectComponent
                        className={'sid-discharge-date__month'}
                        isErrored={isErrored}
                        options={months}
                        onChange={choice => updateDischargeMonth(choice ? choice.value : null)}
                        placeholder={intl.formatMessage({ id: 'dateTime.month', defaultMessage: 'Month' })}
                        value={dischargeDateMonth ? months[dischargeDateMonth - 1] : undefined}
                    />

                    <input
                        className={`sid-discharge-date__day sid-text-input sid-text-input--required ${isErrored ? 'sid-text-input--error' : ''}`}
                        id="sid-discharge-date-day"
                        min={1}
                        max={31}
                        name="sid-discharge-date-day"
                        onChange={e => updateDischargeDay(e.target.value)}
                        onBlur={getDischargeDate}
                        placeholder={intl.formatMessage({ id: 'dateTime.day', defaultMessage: 'Day' })}
                        type="text"
                        value={dischargeDateDay || ''}
                    />

                    <input
                        className={`sid-discharge-date__year sid-text-input sid-text-input--required ${isErrored ? 'sid-text-input--error' : ''}`}
                        id="sid-discharge-date-year"
                        name="sid-discharge-date-year"
                        type="text"
                        min={minDischargeYear}
                        max={thisYear}
                        value={dischargeDateYear || ''}
                        placeholder={intl.formatMessage({ id: 'dateTime.year', defaultMessage: 'Year' })}
                        onChange={e => updateDischargeYear(e.target.value)}
                        onBlur={getDischargeDate}
                    />
                </div>
            </label>

            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id={'errorId.invalidDischargeDate'}
                            defaultMessage="Invalid discharge date"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

DischargeDate.propTypes = {
    value: propTypes.string,
    onChange: propTypes.func.isRequired,
    isErrored: propTypes.bool.isRequired,
};
DischargeDate.defaultProps = {
    value: '2000-01-01',
};

export const DischargeDateComponent = injectIntl(DischargeDate);
