import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { FormFieldComponenentProps } from '../../../lib/types/types';

interface PassedProps {
    explanation?: string | JSX.Element;
}

const EmailComponent = ({ value, isErrored, onChange, explanation }: FormFieldComponenentProps & PassedProps) => {
    return (
        <div className="sid-field sid-email">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label-with-explanation sid-l-space-btm-sm">
                    <div className="sid-field__label sid-field__label--required">
                        <FormattedHTMLMessage
                            id="emailAddress"
                            defaultMessage="Email address"
                        />
                    </div>
                    {
                        explanation ?
                            <div className="sid-field__label-explanation">
                                {explanation}
                            </div>
                        :
                            <FormattedHTMLMessage
                                id="emailExplanation"
                                defaultMessage="Needed to send you your unique code"
                            />
                    }
                </div>
                <input
                    className={
                        `sid-text-input sid-text-input--required
                            sid-l-full-width ${isErrored ? 'sid-text-input--error' : ''}`
                    }
                    id="sid-email"
                    name="sid-email"
                    type="text"
                    value={value}
                    onChange={e => onChange(e.target.value)}
                />
            </label>
            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id="errorId.invalidEmail"
                            defaultMessage="Invalid email"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

export {  EmailComponent };
