import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';
import { FormattedHTMLMessage } from 'react-intl';
import Downshift from 'downshift';

import { FormSelectChoiceShape } from '../../lib/types/propTypes';
// import { FetchOrganizationsComponent } from './FetchOrganizationsComponent';
import { FormSelectChoice } from '../../lib/types/types';
import { InputSelectListComponent } from './InputSelectList/InputSelectListComponent';
import { logger } from '../../lib/logger/logger';
import { InputSelectButtonComponent } from './InputSelectButton/InputSelectButtonComponent';


interface InputSelectProps {
    className?: string;
    disabled?: boolean;
    isErrored?: boolean;
    onChange: any;
    options: FormSelectChoice[];
    placeholder?: string;
    value?: FormSelectChoice;
}

const InputSelect = ({ className, disabled, isErrored, options, onChange, placeholder, value }: InputSelectProps) => {
    const itemToString = (item) => {
        return item ? item.label : '';
    };

    const filterOptions = (options: FormSelectChoice[], inputValue: string): FormSelectChoice[] => {
        const filteredOptions: FormSelectChoice[] = (
            options.filter(option =>
                    !inputValue ||
                    option.label.toLowerCase().includes(inputValue.toLowerCase()) ||
                    (option.value as string).toLowerCase().includes(inputValue.toLowerCase()),
            )
        );
        return filteredOptions;
    };

    return (
        <Downshift
        onChange={onChange}
        itemToString={itemToString}
        initialSelectedItem={value && value.value && value.label ? value : undefined}
        defaultHighlightedIndex={0}>
            {({
                clearSelection,
                getInputProps,
                getItemProps,
                getMenuProps,
                getToggleButtonProps,
                highlightedIndex,
                inputValue,
                isOpen,
                openMenu,
                selectedItem,
            }) =>
            <div className={`sid-input-select-list ${className}`}>
                <input
                    {...getInputProps({ onFocus: openMenu })}
                    className={`sid-l-full-width sid-text-input ${isErrored ? 'sid-text-input--error' : ''}`}
                    disabled={disabled}
                    placeholder={ placeholder }
                />
                <InputSelectButtonComponent
                    disabled={disabled}
                    getToggleButtonProps={getToggleButtonProps}
                    isOpen={isOpen}
                    clearSelection={selectedItem ? clearSelection : undefined}
                />

                {isOpen
                    ?
                    <div>
                        {filterOptions(options, inputValue).length > 0 ?
                            <InputSelectListComponent
                                highlightedIndex={ highlightedIndex }
                                itemToString={ itemToString }
                                options={ filterOptions(options, inputValue) }
                                getItemProps={ getItemProps }
                                getMenuProps={ getMenuProps }
                            />
                            :
                            <div className={'sid-input-select-list__menu'} {...getMenuProps()}>
                                <div className={'sid-input-select-list__message'}>
                                    <FormattedHTMLMessage
                                        id="noOptions"
                                        defaultMessage="No Options"
                                    />
                                </div>
                            </div>
                        }
                    </div>
                : null}
            </div>}
        </Downshift>
    );
};


InputSelect.propTypes = {
    className: propTypes.string,
    disabled: propTypes.bool,
    isErrored: propTypes.bool,
    onChange: propTypes.func.isRequired,
    options: propTypes.array.isRequired,
    placeholder: propTypes.string,
    value: propTypes.object,
};

InputSelect.defaultProps = {
    className: undefined,
    disabled: false,
    isErrored: false,
    placeholder: '',
    value: undefined,
};

export const InputSelectComponent = InputSelect;
