import { Dispatch } from 'redux';
import { logger } from '../logger/logger';
import { DatabaseId, VerificationResponse, ViewModel, BoundAction, ActionCreator, FieldValidationErrors, VerificationStep, ProgramTheme, ActiveMilitaryPersonalInfoViewModel, InactiveMilitaryPersonalInfoViewModel, VerificationService } from '../types/types';
import { assertValidProgramId } from '../types/assertions';
import { VerificationApiClient } from '../ServerApi/VerificationApiClient';
import { getEmptyViewModel } from '../ServerApi/VerificationRequestGetters';
import { VerificationStepsEnum } from '../types/runtimeTypes';

const BoundAction = Promise; // This is weird, see TS1055

// Start a new verification "flow"
export const createFetchNewVerificationRequest: ActionCreator = (
    dispatch: Dispatch,
): BoundAction => async (
    programId: DatabaseId,
    previousViewModel: ViewModel,
) => {
    assertValidProgramId(programId);
    // TODO, is dispatch async? and if so, do we need to wait for the event to propagate?
    dispatch({ type: 'IS_LOADING', isLoading: true });

    try {
        const verificationResponsePromise = VerificationApiClient.fetchNewVerificationRequest(programId);
        return createFetchVerificationRequest(dispatch, verificationResponsePromise, programId, previousViewModel);
    } catch (e) {
        logger.error(e);
    }
};

export const createFetchExistingVerificationRequest: ActionCreator = (
    dispatch: Dispatch,
): BoundAction => async (
    programId: DatabaseId,
    verificationId: string,
    previousViewModel: ViewModel,
) => {
    assertValidProgramId(programId);
    dispatch({ type: 'IS_LOADING', isLoading: true });

    try {
        const verificationResponsePromise = VerificationApiClient.fetchExistingVerificationRequest(verificationId);
        return createFetchVerificationRequest(dispatch, verificationResponsePromise, programId, previousViewModel);

    } catch (e) {
        logger.error(e);
    }
};

const createFetchVerificationRequest = async (
    dispatch: Dispatch,
    verificationResponsePromise: Promise<VerificationResponse>,
    programId: DatabaseId,
    previousViewModel?: ViewModel,
) => {
    try {
        const programThemePromise = VerificationApiClient.fetchProgramTheme(programId);
        const programTheme = await programThemePromise;
        const verificationResponse = await verificationResponsePromise;

        logger.info('verificationResponse received: ', verificationResponse);

        let viewModel: ViewModel;

        if (previousViewModel) {
            logger.info('Verification request started with previously-entered request data', previousViewModel);
            viewModel = previousViewModel;
        } else {
            logger.info('Verification request started with empty request data');
            viewModel = getEmptyViewModel(verificationResponse);
        }

        const promiseVerificationResponse = dispatch({ verificationResponse, type: 'VERIFICATION_RESPONSE' });
        const promiseViewModel = dispatch({ viewModel, type: 'VIEW_MODEL' });
        const promiseProgramTheme = dispatch({ programTheme, type: 'PROGRAM_THEME' });
        const promiseIsLoading = dispatch({ type: 'IS_LOADING', isLoading: false });
        return Promise.all([promiseVerificationResponse, promiseViewModel, promiseProgramTheme, promiseIsLoading]);
    } catch (e) {
        logger.error(e);
    }
};


// Update viewModel in redux state.
export const createUpdateViewModel: ActionCreator = (
    dispatch: Dispatch,
): BoundAction => async (
    viewModel: ViewModel,
) => dispatch({ viewModel, type: 'VIEW_MODEL' });

export const createUpdateFieldValidationErrors: ActionCreator = (
    dispatch: Dispatch,
): BoundAction => async (
    fieldValidationErrors: FieldValidationErrors,
) => dispatch({ fieldValidationErrors, type: 'FIELD_VALIDATION_ERRORS' });

// Submit a step to the backend REST API
export const createSubmitStep: ActionCreator = (
    dispatch: Dispatch,
): BoundAction => async (stepName: VerificationStep, viewModel: ViewModel, previousResponse: VerificationResponse) => {
    logger.log(`submitStep(): submitting stepName ${stepName}, viewModel:`, viewModel);

    await dispatch({ type: 'IS_LOADING', isLoading: true });

    try {
        logger.info(`submitting step ${stepName}`, viewModel);
        const verificationResponse: VerificationResponse = await VerificationApiClient.submitStep(stepName, previousResponse, viewModel);
        const currentStep = verificationResponse.currentStep;

        // TODO: Find a better way to persist data between military steps.
        // Ensure the new viewModel for military steps persists data from the previous military step.
        let newViewModel: ViewModel;

        if (currentStep === VerificationStepsEnum.collectActiveMilitaryPersonalInfo) {
            newViewModel = { ...viewModel, dischargeDate: '' };
            delete newViewModel['dischargeDate'];
        } else if (currentStep === VerificationStepsEnum.collectInactiveMilitaryPersonalInfo) {
            if (!(viewModel as InactiveMilitaryPersonalInfoViewModel).dischargeDate) {
                newViewModel = { ...viewModel, dischargeDate: '' };
            } else {
                newViewModel = viewModel;
            }
        } else { // If not military, get an empty viewModel
            newViewModel = getEmptyViewModel(verificationResponse);
        }

        const promiseVerificationResponse = dispatch({ verificationResponse, type: 'VERIFICATION_RESPONSE' });
        const promiseViewModel = dispatch({ viewModel: newViewModel, type: 'VIEW_MODEL' });
        const promiseIsLoading = dispatch({ type: 'IS_LOADING', isLoading: false });
        return Promise.all([promiseVerificationResponse, promiseViewModel, promiseIsLoading]);
    } catch (e) {
        this.handleError(e);
    }
};
