
import { logger } from '../logger/logger';
import { VerificationStep, VerificationResponse, MockStep } from '../types/types';
import { MockStepsEnum, VerificationStepsEnum } from '../types/runtimeTypes';
import { getOptions } from '../../options/options';
import { QUERY_STRING_STEP_OVERRIDE } from '../../constants';

import {
    mockStudentPersonalInfoResponse,
    mockTeacherPersonalInfoResponse,
    mockDocUploadVerificationResponse,
    mockSuccessResponse,
    mockErrorResponse,
    mockPendingResponse,
    mockDocReviewLimitExceededResponse,
} from '../../serverMocks/student';

// To load a specific step directly, visit e.g. localhost:1234/?step=success
export const getRouteOverride = (): MockStep => {
    try {
        const parsed: URLSearchParams = new URLSearchParams(window.location.search);

        if (parsed.get(QUERY_STRING_STEP_OVERRIDE)) {
            return <VerificationStep> parsed.get(QUERY_STRING_STEP_OVERRIDE);
        }
        if (getOptions().mockStep) {
            return getOptions().mockStep;
        }
        return null;
    } catch (e) {
        logger.error(e);
    }
};

// Special case. The loading screen is not a step, so mocking it is a little different.
export const isMockingLoading = (): boolean => {
    try {
        const parsed: URLSearchParams = new URLSearchParams(window.location.search);
        return (parsed.get(QUERY_STRING_STEP_OVERRIDE) === MockStepsEnum.loading || getOptions().mockStep === MockStepsEnum.loading);
    } catch (e) {
        logger.error(e);
    }
};

export const getOverriddenMock = (overriddenStep: MockStep): VerificationResponse => {
    let mockResponse;
    switch (overriddenStep) {
        case VerificationStepsEnum.success:
            mockResponse = mockSuccessResponse;
            logger.info('getOverriddenMock: mocking success response');
            break;
        case VerificationStepsEnum.docUpload:
            mockResponse = mockDocUploadVerificationResponse;
            logger.info('getOverriddenMock: mocking docUpload response');
            break;
        case VerificationStepsEnum.pending:
            mockResponse = mockPendingResponse;
            logger.info('getOverriddenMock: mocking pending response');
            break;
        case VerificationStepsEnum.docReviewLimitExceeded:
            mockResponse = mockDocReviewLimitExceededResponse;
            logger.info('getOverriddenMock: mocking docReviewLimitExceeded response');
            break;
        case VerificationStepsEnum.error:
            mockResponse = mockErrorResponse;
            logger.info('getOverriddenMock: mocking error response');
            break;
        case VerificationStepsEnum.collectStudentPersonalInfo:
            mockResponse = mockStudentPersonalInfoResponse;
            logger.info('getOverriddenMock: mocking mockStudentPersonalInfoResponse response');
            break;
        case VerificationStepsEnum.collectTeacherPersonalInfo:
            mockResponse = mockTeacherPersonalInfoResponse;
            logger.info('getOverriddenMock: mocking mockTeacherPersonalInfoResponse response');
            break;
        default:
            mockResponse = null;
            logger.info(`
                getOverriddenMock: not mocking response.
                You may mock a step response by adding things like ?mockStep=success to your route.
                ${overriddenStep}
            `);
    }
    return mockResponse;
};
