import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';

interface PostalCodeInputProps {
    value: any;
    onChange: any;
    isValid? : boolean;
}

const PostalCodeInput = ({
    value,
    onChange,
    isValid,
}: PostalCodeInputProps) => {

    return (
        <input
            className={`sid-text-input sid-text-input--required
                sid-l-full-width ${isValid ? '' : 'sid-text-input--error'}`}
            id="sid-postal-code"
            name="sid-postal-code"
            type="text"
            value={value}
            onChange={e => onChange(e.target.value)}
        />
    );
};

PostalCodeInput.propTypes = {
    value: propTypes.string,
    isValid: propTypes.bool,
    onChange: propTypes.func.isRequired,
};
PostalCodeInput.defaultProps = {
    value: '',
    isValid: true,
};

export const PostalCodeInputComponent = PostalCodeInput;
