import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';

interface OptInInputProps {
    onChange: any;
    value: string;
}

const OptInInput = ({
    onChange,
    value,
}: OptInInputProps) => {

    return (
        <div className="sid-checkbox">
            <input
                className="sid-checkbox__input"
                id="sid-opt-in"
                name="sid-opt-in"
                type="checkbox"
                checked={value && value === 'true' ? true : false}
                onChange={e => onChange(e.target.checked ? 'true' : 'false')}
            />
            <span className="sid-checkbox__input-like"></span>
        </div>
    );
};

OptInInput.propTypes = {
    onChange: propTypes.func.isRequired,
    value: propTypes.string,
};

OptInInput.defaultPropTypes = {
    value: '',
};

export const OptInInputComponent = OptInInput;
