import * as React from 'react'; // https://github.com/parcel-bundler/parcel/issues/1199
import * as propTypes from 'prop-types';
import { FormattedHTMLMessage, InjectedIntlProps, injectIntl, intlShape } from 'react-intl';
import { logger } from '../../../lib/logger/logger';
import { FormFieldComponenentProps, FormSelectChoice } from '../../../lib/types/types';
import { InputSelectComponent } from '../../InputSelect/InputSelectComponent';

export const BirthDate = ({
    onChange,
    intl,
    isErrored,
    value,
}: FormFieldComponenentProps & InjectedIntlProps) => {
    const birthDateParts = value.split('-');
    const birthDateYear = Number.parseInt(birthDateParts[0], 10) || undefined;
    const birthDateMonth = Number.parseInt(birthDateParts[1], 10) || undefined;
    const birthDateDay = Number.parseInt(birthDateParts[2], 10) || undefined;

    const minBirthYear = 1900;
    const thisYear = new Date().getFullYear();

    const months: FormSelectChoice[] = [
        { value: '1', label: intl.formatMessage({ id: 'dateTime.january', defaultMessage: 'January' }) },
        { value: '2', label: intl.formatMessage({ id: 'dateTime.february', defaultMessage: 'February' }) },
        { value: '3', label: intl.formatMessage({ id: 'dateTime.march', defaultMessage: 'March' }) },
        { value: '4', label: intl.formatMessage({ id: 'dateTime.april', defaultMessage: 'April' }) },
        { value: '5', label: intl.formatMessage({ id: 'dateTime.may', defaultMessage: 'May' }) },
        { value: '6', label: intl.formatMessage({ id: 'dateTime.june', defaultMessage: 'June' }) },
        { value: '7', label: intl.formatMessage({ id: 'dateTime.july', defaultMessage: 'July' }) },
        { value: '8', label: intl.formatMessage({ id: 'dateTime.august', defaultMessage: 'August' }) },
        { value: '9', label: intl.formatMessage({ id: 'dateTime.september', defaultMessage: 'September' }) },
        { value: '10', label: intl.formatMessage({ id: 'dateTime.october', defaultMessage: 'October' }) },
        { value: '11', label: intl.formatMessage({ id: 'dateTime.november', defaultMessage: 'November' }) },
        { value: '12', label: intl.formatMessage({ id: 'dateTime.december', defaultMessage: 'December' }) },
    ];

    const updateBirthMonth = (month) => {
        if (month === '' || month === null) {
            onChange(`${birthDateYear}--${birthDateDay}`);
        }

        const newVal = Number.parseInt(month, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= 12) {
            onChange(`${birthDateYear}-${month}-${birthDateDay}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid birth month`);
        }
    };

    const updateBirthDay = (day) => {
        if (day === '') {
            onChange(`${birthDateYear}-${birthDateMonth}-`);
        }
        const newVal = Number.parseInt(day, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= 31) {
            onChange(`${birthDateYear}-${birthDateMonth}-${newVal}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid birth day`);
        }
    };

    const updateBirthYear = (year) => {
        if (year === '') {
            onChange(`-${birthDateMonth}-${birthDateDay}`);
        }
        const newVal = Number.parseInt(year, 10);
        if (typeof newVal === 'number' && newVal >= 1 && newVal <= thisYear) {
            onChange(`${newVal}-${birthDateMonth}-${birthDateDay}`);
        } else {
            logger.error(`Value '${newVal}' is an invalid birth year`);
        }
    };

    const getBirthDate = () => {
        onChange(`${birthDateYear}-${birthDateMonth}-${birthDateDay}`);
    };

    return (
        <div className="sid-field sid-birthdate">
            <div className="sid-l-space-top-md" />
            <label>
                <div className="sid-field__label-with-explanation sid-l-space-btm-sm">
                    <div className="sid-field__label sid-field__label--required">
                        <FormattedHTMLMessage
                            id="birthDate"
                            defaultMessage="Date of birth"
                        />
                    </div>
                    <div className="sid-field__label-explanation">
                        <FormattedHTMLMessage
                            id="verificationPurposesOnly"
                            defaultMessage="Used for verification purposes only"
                        />
                    </div>
                </div>
            </label>

            <div className="sid-birthdate__inputs">
                <InputSelectComponent
                    className={'sid-birthdate__month'}
                    isErrored={isErrored}
                    options={months}
                    onChange={choice => updateBirthMonth(choice ? choice.value : null)}
                    placeholder={intl.formatMessage({ id: 'dateTime.month', defaultMessage: 'Month' })}
                    value={birthDateMonth ? months[birthDateMonth - 1] : undefined}
                />

                <input
                    className={`sid-birthdate__day sid-text-input sid-text-input--required ${isErrored ? 'sid-text-input--error' : ''}`}
                    id="sid-birthdate-day"
                    min={1}
                    max={31}
                    name="sid-birthdate-day"
                    onChange={e => updateBirthDay(e.target.value)}
                    onBlur={getBirthDate}
                    placeholder={intl.formatMessage({ id: 'dateTime.day', defaultMessage: 'Day' })}
                    type="text"
                    value={birthDateDay || ''}
                />

                <input
                    className={`sid-birthdate__year sid-text-input sid-text-input--required ${isErrored ? 'sid-text-input--error' : ''}`}
                    id="sid-birthdate-year"
                    name="sid-birthdate-year"
                    type="text"
                    min={minBirthYear}
                    max={thisYear}
                    value={birthDateYear || ''}
                    placeholder={intl.formatMessage({ id: 'dateTime.year', defaultMessage: 'Year' })}
                    onChange={e => updateBirthYear(e.target.value)}
                    onBlur={getBirthDate}
                />
            </div>

            {
                isErrored ? (
                    <div className="sid-field-error">
                        <FormattedHTMLMessage
                            id={'errorId.invalidBirthDate'}
                            defaultMessage="Invalid birth date"
                        />
                    </div>
                ) : null
            }
        </div>
    );
};

BirthDate.propTypes = {
    value: propTypes.string,
    onChange: propTypes.func.isRequired,
    isErrored: propTypes.bool.isRequired,
    intl: intlShape.isRequired,
};
BirthDate.defaultProps = {
    value: '2000-01-01',
};

export const BirthDateComponent = injectIntl(BirthDate);
