import * as React from 'react';
import { FormattedHTMLMessage } from 'react-intl';
import { VerificationService } from '../../lib/types/types';
import { FaqLinkComponent } from '../FaqLink/FaqLinkComponent';

interface HowDoesVerifyingWorkProps {
    verificationService: VerificationService;
}

interface HowDoesVerifyingWorkState {
    tooltipIsOpen: boolean;
}

export class HowDoesVerifyingWorkComponent extends React.Component<HowDoesVerifyingWorkProps, HowDoesVerifyingWorkState> {

    constructor(props: HowDoesVerifyingWorkProps) {
        super(props);
        this.state = {
            tooltipIsOpen: false,
        };
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }

    showTooltip() {
        this.setState({
            tooltipIsOpen: true,
        });
    }

    hideTooltip() {
        this.setState({
            tooltipIsOpen: false,
        });
    }

    public render() {
        return (
            <div className="sid-how-verify-works" onMouseEnter={this.showTooltip} onMouseLeave={this.hideTooltip}>
                <span className="sid-link">
                    <FormattedHTMLMessage id="howDoesVerifyingWork"
                        defaultMessage="How does verifying work?" />
                </span>
                {
                    this.state.tooltipIsOpen ?
                        <div className="sid-how-verify-works__container">
                            <div className="sid-how-verify-works__caret" />
                            <div className="sid-how-verify-works__tooltip">
                                <FormattedHTMLMessage id="step.personalInfo.howDoesVerifyingWorkDetails"
                                    defaultMessage="-"
                                    values={{
                                        company: this.props.verificationService.programTheme.intl.messages.companyName || '{Company}',
                                    }} />
                                &nbsp;
                                <FaqLinkComponent verificationService={this.props.verificationService} />
                            </div>
                        </div>
                        :
                        null
                }
            </div>
        );
    }
}
